<template>
  <div class="pageContent">
    <el-card  shadow="hover" class="box-card box">
      <el-radio @change="changeType(0)" v-model="payType" label="0">微信支付 - 代销</el-radio>
      <div class="font">筷赚已代为申请微信支付，可立即使用微信支付完成收款。可以在店铺余额查看货款并提现，筷赚待售微信支付交易手续费。</div>
    </el-card>
    <el-card  shadow="hover" class="box-card box" style="margin-top:20px;">
      <el-radio @change="changeType(1)" :disabled="mpPayStatus == 0 || mpAuthStatus == 0|| miniPayStatus == 0  || miniAuthStatus == 0" v-model="payType" label="1">微信支付 - 自有</el-radio>
      <div class="font flex-row-start">
        <i v-if="mpAuthStatus > 0" class="el-icon-circle-check" style="color:#67C23A;margin-right:10px;"></i>
        <i v-if="mpAuthStatus == 0" class="el-icon-error" style="color:#F56C6C;margin-right:10px;"></i>
        店铺需先去授权公众号
        <el-button @click="mpAuth" style="margin-left:40px;" :disabled="mpAuthStatus > 0" type="primary">去授权</el-button>
      </div>
      <div class="font flex-row-start">
        <i v-if="mpPayStatus > 0" class="el-icon-circle-check" style="color:#67C23A;margin-right:10px;"></i>
        <i v-if="mpPayStatus == 0" class="el-icon-error" style="color:#F56C6C;margin-right:10px;"></i>
        店铺需绑定“认证公众号”，开通公众号“微信支付权限”，并完成支付参数配置。
        <el-button @click="mpConfig" style="margin-left:40px;" :disabled="mpAuthStatus == 0" type="primary">支付配置</el-button>
      </div>
      <div class="font mt40 flex-row-start">
        <i v-if="miniAuthStatus > 0" class="el-icon-circle-check" style="color:#67C23A;margin-right:10px;"></i>
        <i v-if="miniAuthStatus == 0" class="el-icon-error" style="color:#F56C6C;margin-right:10px;"></i>
        店铺需先去授权小程序
        <el-button @click="miniAuth" style="margin-left:40px;" :disabled="miniAuthStatus > 0" type="primary">去授权</el-button>
      </div>
      <div class="font flex-row-start">
        <i v-if="miniPayStatus > 0" class="el-icon-circle-check" style="color:#67C23A;margin-right:10px;"></i>
        <i v-if="miniPayStatus == 0" class="el-icon-error" style="color:#F56C6C;margin-right:10px;"></i>
        店铺需绑定“认证公众号”，开通公众号“微信支付权限”，并完成支付参数配置。
        <el-button @click="miniConfig" style="margin-left:40px;" :disabled="miniAuthStatus == 0" type="primary">支付配置</el-button>
      </div>
    </el-card>
  </div>
</template>
<script>
import { mapState } from 'vuex'
import { getShopPayType , setShopPayType } from '@/api/moneyConfig/payConfig'
export default {
  data(){
    return{
      payType:0,
      mpPayStatus:0,
      miniPayStatus:0,
      mpAuthStatus:0,
      miniAuthStatus:0
    }
  },
  created(){
    this.getData();
  },
  methods:{
    // 获取数据
    getData(){
      getShopPayType(this.shop.shopId).then(res=>{
        console.log('店铺配置',res)
        this.payType = res.data.payType + ''
        this.mpPayStatus = res.data.mpPayStatus + ''
        this.miniPayStatus = res.data.miniPayStatus + ''
        this.mpAuthStatus = res.data.mpAuthStatus
        this.miniAuthStatus = res.data.miniAuthStatus
      })
    },
    // 变更配置
    changeType(i){
      console.log('变更了配置')
      setShopPayType(this.shop.shopId,i).then(res=>{
        console.log('变更配置',res)
        if(res.code == 200){
          this.$message({
            message: '更改成功',
            type: 'success'
          });
          this.getData();
        }else{
          this.$message.error(res.message);
        }
      })
    },
    // 公众号授权
    mpAuth(){
      this.$router.push({
        path:'/weChat/mpManage'
      })
    },
    // 公众号配置
    mpConfig(){
      this.$router.push({
        path:'/moneyConfig/payConfig/configList',
        query:{
          payType:1
        }
      })
    },
    // 小程序授权
    miniAuth(){
      this.$router.push({
        path:'/weChat/miniManage'
      })
    },
    // 小程序配置
    miniConfig(){
      this.$router.push({
        path:'/moneyConfig/payConfig/configList',
        query:{
          payType:2
        }
      })
    },
  },
  computed: {
    ...mapState([
      'user',
      'shop'
    ])
  },
}
</script>
<style lang="less" scoped>
  .activeBox{
    border-color: #409EFF;
  }
  .font{
    padding-left: 24px;
    color: #909399;
    margin-top: 10px;
    &.mt40{
      margin-top: 50px;
    }
  }
</style>